.inspiration-page {
  margin: 0 40px;
  text-align: justify;
  overflow-y: auto;
  padding-right: 10px;
}

.inspiration-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

.inspiration-title h1 {
  color: rgb(35, 49, 66);
  font-size: 3rem;
  font-size: 700;
}

.inspiration-title img {
  height: 70px;
  width: 70px;
  margin-right: 20px;
}

.inspiration-text {
  padding: 0;
  font-size: 1.125rem;
}

.problem {
  text-align: start;
  margin: 20px 20px 40px;
  font-size: 1.125rem;
}

.problem-title {
  font-weight: 700;
}

.problem-description {
  font-style: italic;
  margin-bottom: 10px;
}

.problem-graph {
  display: grid;
  grid-template-columns: 1fr 180px 1fr;
  gap: 3rem;
  text-align: center;
  margin-bottom: 10px;
}

.problem-graph table {
  border: 1px solid black;
  border-collapse: collapse;
}

.problem-graph tr {
  border: 1px solid black;
}

.problem-graph td {
  border: 1px solid black;
  padding: 0;
}

.arrow-indicator {
  height: fit-content;
  margin: auto;
}

.grid-example-land {
  background-color: rgba(64, 42, 35, 1);
}

.grid-example-water {
  background-color: rgba(0, 190, 218, 0.75);
}


@media only screen and (max-width: 1440px) {
  /* For laptop */

  .inspiration-page {
    margin: 0 20px;
  }

  .inspiration-title {
    margin-bottom: 20px;
  }

  .inspiration-title h1 {
    font-size: 2rem;
  }

  .inspiration-title img {
    height: 50px;
    width: 50px;
    margin-right: 10px;
  }

  .inspiration-text {
    font-size: 0.8125rem;
  }

  .problem {
    margin: 10px 20px 20px;
    font-size: 0.8125rem;
  }

  .problem-description {
    margin-bottom: 5px;
  }

  .problem-graph {
    gap: 1rem;
  }

}