.btn-nav {
  cursor: pointer;
  border-radius: 6px;
  font-size: 1.125rem;
  font-weight: 600;
  padding: 8px 25px;
  margin: auto 8px;
  width: 180px;
  height: 50px;
}

.btn-disabled {
  background-color: transparent;
  border: 2px solid rgba(56, 81, 112, 0.6);
  color: rgba(56, 81, 112, 0.6);
  pointer-events: none;
}

.btn-default {
  background-color: transparent;
  border: 2px solid #fff;
  color: #fff;
  transition: all 0.3s ease-out;
}

.btn-default:hover {
  background-color: rgb(35, 49, 66);
  border: 2px solid #fff;
  color: #fff;
  transition: all 0.3s ease-out;
}

.btn-searching {
  background-color: rgb(56, 81, 112);
  border: 2px solid rgb(224, 235, 235);
  color: rgb(224, 235, 235);
  pointer-events: none;
}

.btn-end {
  color: rgb(188, 37, 37);
  border: 2px solid rgb(188, 37, 37);
  transition: all 0.3s ease-out;
}

.btn-end:hover {
  background-color: rgb(188, 37, 37);
  color: rgb(224, 235, 235);
  transition: all 0.3s ease-out;
}


@media only screen and (max-width: 1440px) {
  /* For Laptop */
  .btn-nav {   
    font-size: 1rem;
    padding: 6px 20px;
    margin: auto 6px;
    width: 150px;
    height: 45px;
  }
}